@import url("https://fonts.googleapis.com/css2?family=Cabin:wght@400;500;700&display=swap");
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

html {
  font-size: 47.5%;
  scroll-behavior: smooth;
}

.purple {
  color: #8781BD;
}

.blue {
  color: #6DCFF6;
}

.pink {
  color: #F49AC1;
}

.purple-bg {
  background-color: #8781BD;
}

.blue-bg {
  background-color: #6DCFF6;
}

.pink-bg {
  background-color: #F49AC1;
}

body {
  font-family: "Spartan", sans-serif;
  min-height: 100vh;
  background-image: url("images/bg.jpg");
  background-size: 100vw;
  position: relative;
  overflow-x: hidden;
  scrollbar-color: #6DCFF6 #F49AC1;
}

.container {
  max-width: 130rem;
  padding: 6.5rem 10rem 0 10rem;
  margin: 0 auto;
}

.header {
  display: flex;
  align-items: center;
}

.navButton {
  width: 5rem;
  height: 2.2rem;
  display: none;
  cursor: pointer;
}

.navline {
  width: 5rem;
  height: 0.4rem;
  transition: all 0.2s ease;
}
.navline.blue {
  transform-origin: left;
}
.navline.purple {
  transform-origin: right;
}

.logoHolder {
  margin-right: auto;
}

.logoImage {
  height: 5rem;
  width: auto;
}

.logoIntro {
  height: 50vh;
  width: auto;
}

.logoClick {
  cursor: pointer;
}

.linksHolder {
  display: flex;
  justify-content: space-between;
  align-items: center;
  list-style: none;
}
.linksHolder-mobile {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 5rem;
  list-style: none;
  transition: all 0.2s ease;
}

.linksMobile {
  max-height: 0;
  display: none;
}

.navOn {
  max-height: 14rem;
  display: block;
}

.listItem {
  transition: all 0.2s ease;
}
.listItem:hover {
  transform: translateY(-2px);
  text-shadow: rgba(0, 0, 0, 0.1) 0 1px 5px;
}
.listItem-mobile {
  transition: all 0.2s ease;
}
.listItem-mobile:not(:last-child) {
  margin-bottom: 2rem;
}
.listItem-mobile:hover {
  transform: translateY(-2px);
  text-shadow: rgba(0, 0, 0, 0.1) 0 1px 5px;
}

.link {
  text-decoration: none;
  font-size: 1.8rem;
  text-transform: uppercase;
  font-weight: 600;
  letter-spacing: 15.5%;
}

.margin {
  margin-right: 5rem;
}

.main {
  max-width: 100%;
  position: relative;
  margin-top: 6.8rem;
  outline: 0.4rem solid #8781BD;
  max-height: 75rem;
}

.slider {
  height: 61rem;
  background-image: url("./images/3x3---full.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position-x: center;
  background-position-y: 50%;
}

.slide-1 {
  max-width: 100%;
  height: auto;
}
.slide-title {
  width: 42rem;
  height: 11rem;
  background-color: #fff;
  position: absolute;
  z-index: 3;
  bottom: 0;
  right: 0;
  outline: 0.4rem solid #8781BD;
  display: flex;
  align-items: center;
  justify-content: center;
}
.slide-controls {
  width: 42rem;
  height: 11rem;
  background-color: #fff;
  position: absolute;
  z-index: 3;
  bottom: -11.35rem;
  right: 0;
  outline: 0.4rem solid #8781BD;
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.slide-social {
  width: 22rem;
  height: 11rem;
  background-color: #fff;
  position: absolute;
  z-index: 3;
  bottom: -11.35rem;
  left: 0;
  outline: 0.4rem solid #8781BD;
  display: flex;
  align-items: center;
  justify-content: space-around;
}
.slide-social-bar {
  width: 0.4rem;
  height: 100%;
  background-color: #8781BD;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}

.iconsocial {
  font-size: 4rem;
  cursor: pointer;
  transition: all 0.2s ease;
}
.iconsocial:hover {
  transform: translateY(-2px);
  text-shadow: rgba(0, 0, 0, 0.1) 0 1px 5px;
}
.iconsocial-pink {
  color: #F49AC1;
}
.iconsocial-blue {
  color: #6DCFF6;
}

.controls-back, .controls-fwd {
  font-size: 6rem;
  font-weight: 700;
  cursor: pointer;
  transition: all 0.2s ease;
}
.controls-back:hover, .controls-fwd:hover {
  transform: translateY(-2px);
  text-shadow: rgba(0, 0, 0, 0.1) 0 1px 5px;
}
.controls-back {
  color: #6DCFF6;
}
.controls-fwd {
  color: #F49AC1;
}
.controls-bar {
  width: 0.4rem;
  height: 100%;
  background-color: #8781BD;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
}

.arrow {
  max-width: 100%;
  height: auto;
  z-index: 3;
}

.o {
  width: 2.5rem;
  height: 2.5rem;
  position: absolute;
  z-index: 4;
}

.o-left-top {
  left: -1.25rem;
  top: -1.25rem;
}
.o-left-bottom {
  left: -1.25rem;
  bottom: -1.25rem;
}
.o-left-bottom-social-1 {
  left: -1.25rem;
  bottom: -12.75rem;
}
.o-left-bottom-social-2 {
  left: 20.75rem;
  bottom: -12.75rem;
}
.o-left-bottom-social-3 {
  left: 20.75rem;
  bottom: -1.25rem;
}
.o-right-top {
  right: -1.25rem;
  top: -1.25rem;
}
.o-right-bottom {
  right: -1.25rem;
  bottom: -1.25rem;
}
.o-right-bottom-text {
  right: -1.25rem;
  bottom: 10rem;
}
.o-right-bottom-text-2 {
  right: 41rem;
  bottom: 10rem;
}
.o-right-bottom-text-3 {
  right: 41rem;
  bottom: -1.25rem;
}
.o-right-bottom-controls-1 {
  bottom: -12.75rem;
  right: -1.25rem;
}
.o-right-bottom-controls-2 {
  bottom: -12.75rem;
  right: 41rem;
}

.despre {
  max-width: 100%;
  position: relative;
  margin-top: 6.8rem;
}
.despre-intro, .despre-membri {
  display: flex;
  align-items: center;
  flex-direction: column;
}

.separator {
  width: 2.5rem;
  height: 2.5rem;
  text-align: center;
}

.card-membru {
  display: flex;
  align-items: center;
  margin-bottom: 5rem;
  min-height: 30rem;
}
.card-membru-appearance {
  position: relative;
}
.card-membru:last-of-type {
  margin-bottom: 20rem;
}

.membru-img {
  border-radius: 50%;
  max-height: 25rem;
  width: auto;
  transition: all 0.2s ease;
  margin-right: 5rem;
}
.membru-img:hover {
  outline-offset: 0.3rem;
  filter: brightness(75%);
}
.membru-nume {
  text-align: center;
  font-size: 3rem;
  font-weight: 600;
  position: absolute;
  left: 50%;
  bottom: 0;
  transform: translate(-75%, 0);
  opacity: 0;
  backface-visibility: hidden;
  transition: all 0.2s ease;
  pointer-events: none;
}
.membru-img:hover + .membru-nume {
  bottom: 50%;
  transform: translate(-75%, 50%);
  opacity: 1;
}

.geo-fix {
  margin-left: 1.75rem;
}

.cards {
  margin-top: 5rem;
}

.outline-purple {
  outline: solid 0.75rem #8781BD;
}
.outline-pink {
  outline: solid 0.75rem #F49AC1;
}
.outline-blue {
  outline: solid 0.75rem #6DCFF6;
}

.back-top {
  z-index: 5;
  display: none;
  position: fixed;
  bottom: 10rem;
  right: 10rem;
  font-size: 6rem;
  font-weight: 700;
  cursor: pointer;
  transition: all 0.2s ease;
  text-decoration: none;
  color: #8781BD;
}
.back-top:hover {
  transform: translateY(-2px);
  text-shadow: rgba(0, 0, 0, 0.1) 0 1px 5px;
}

.proiecte {
  margin: 10rem 0;
}

.proiect {
  margin-bottom: 20rem;
}
.proiect-banner {
  position: relative;
}
.proiect-img {
  width: 100%;
  height: auto;
  outline: solid 0.5rem #8781BD;
}
.proiect-text {
  width: 80%;
  margin: 5rem auto;
  text-align: center;
}

.link-to {
  font-size: 2.4rem;
  color: #F49AC1;
  margin-top: -2rem;
  text-decoration: underline;
  text-underline-offset: 5px;
  transition: all 0.2s ease;
}
.link-to:hover {
  font-size: 2.5rem;
  text-underline-offset: 7px;
}

.parteneri {
  margin: 2.5rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.parteneri-logos {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  margin-top: 5rem;
}

.partener-logo {
  max-width: 30rem;
  height: auto;
}

.partener-link:not(:last-of-type) {
  margin-right: 15rem;
}

.contact {
  margin: 10rem 0;
}
.contact-title {
  line-height: 3.4rem;
  font-size: 2.4rem;
  color: #8781BD;
  text-transform: uppercase;
  margin-bottom: 5rem;
}
.contact-title:nth-of-type(2) {
  margin-top: 10rem;
}

.form {
  display: flex;
  flex-direction: column;
}
.form-input {
  font-family: "Spartan", sans-serif;
  font-size: 1.5rem;
  height: 3rem;
  padding: 1rem;
  margin-bottom: 2rem;
  color: #8781BD;
  text-transform: uppercase;
  font-weight: 400;
  letter-spacing: 0.1rem;
}
.form-input::placeholder {
  color: #8781BD;
  text-transform: uppercase;
  font-weight: 400;
}
.form-input-email {
  width: 60%;
}
.form-input-text {
  width: 60%;
  height: 20rem;
  color: #8781BD;
  text-transform: uppercase;
  font-weight: 400;
}
.form-input-nume, .form-input-prenume {
  width: 45%;
}
.form-nume {
  width: 62%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.form-button {
  font-family: "Spartan", sans-serif;
  width: 15rem;
  height: 3rem;
  padding: 1rem;
  color: #fff;
  text-transform: uppercase;
  letter-spacing: 0.1rem;
  font-weight: 400;
  background-color: #8781BD;
  border: solid 1px #fff;
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.2s ease-in;
}
.form-button:hover {
  background-color: #fff;
  border: solid 1px #8781BD;
  color: #8781BD;
}

.telefon {
  font-size: 2.4rem;
  font-weight: 600;
  margin-bottom: 2rem;
  color: #8781BD;
  letter-spacing: 0.1rem;
  text-transform: uppercase;
}
.telefon-nr {
  color: #F49AC1;
}

